package com.wakaztahir.sketchable.brushes

import androidx.compose.runtime.Composable
import androidx.compose.runtime.getValue
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.setValue
import androidx.compose.ui.geometry.Offset
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.graphics.drawscope.DrawScope
import androidx.compose.ui.graphics.drawscope.Stroke
import com.wakaztahir.sketchable.layers.ColoredPath
import com.wakaztahir.sketchable.model.Brush
import com.wakaztahir.sketchable.model.SketchableLayer
import com.wakaztahir.sketchable.model.SketchableState
import com.wakaztahir.sketchable.model.UndoRedoAction

class EraserBrush(
    var size: Float = 8f,
    private val ringWidth: Float = 2f,
    private val ringColor: Color = Color.Black
) : Brush {

    private var eventCanceled = false

    var removeShapes by mutableStateOf(true)

    private var moveX by mutableStateOf(0f)
    private var moveY by mutableStateOf(0f)

    private var display by mutableStateOf(false)

    override fun handleDownEvent(state: SketchableState, position: Offset) {
        moveX = position.x
        moveY = position.y
        display = true
    }

    override fun handleMoveEvent(state: SketchableState, position: Offset) {

        if(eventCanceled) return

        moveX = position.x
        moveY = position.y

        fun removeWithUndo(index: Int, layer: SketchableLayer) {
            state.layers.remove(layer)
            state.addUndoRedoAction(object : UndoRedoAction {
                override fun action() {
                    state.layers.add(index, layer)
                }

                override fun opposite() {
                    state.layers.remove(layer)
                }
            })
        }

        for (index in (state.layers.size - 1) downTo 0) {
            when (val layer = state.layers[index]) {
                is ColoredPath -> {
                    val removed = layer.removePathsIntersecting(position, this.size)
                    if (removed != null) {
                        state.addUndoRedoAction(object : UndoRedoAction {
                            override fun action() {
                                layer.addPathOperations(removed.index, removed.value)
                            }

                            override fun opposite() {
                                layer.removePathOperations(removed.value)
                            }
                        })
                        return
                    }
                }
            }
        }
    }

    override fun cancelEvent(state: SketchableState) {
        eventCanceled = true
    }

    override fun handleUpEvent(state: SketchableState, position: Offset) {
        display = false
        eventCanceled = false
    }

    override fun DrawScope.draw(state: SketchableState) {
        if (display) {
            drawCircle(
                color = ringColor,
                style = Stroke(width = ringWidth),
                radius = this@EraserBrush.size - ringWidth,
                center = Offset(x = moveX, y = moveY)
            )
        }
    }

    companion object Properties : Brush.Properties {
        @Composable
        override fun brushName(): String = "Eraser"
        override fun DrawScope.drawPreview(color: Color) {
            // do nothing
        }
    }

    override val properties: Brush.Properties
        get() = Properties

}