package com.wakaztahir.sketchable.components.tools

import androidx.compose.foundation.*
import androidx.compose.foundation.layout.*
import androidx.compose.material3.*
import androidx.compose.runtime.*
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.unit.ExperimentalUnitApi
import androidx.compose.ui.unit.TextUnit
import androidx.compose.ui.unit.TextUnitType
import androidx.compose.ui.unit.dp
import com.godaddy.android.colorpicker.ClassicColorPicker
import com.godaddy.android.colorpicker.HsvColor
import com.wakaztahir.helpers.DialogBox
import com.wakaztahir.sketchable.model.PointerMode
import com.wakaztahir.sketchable.model.SketchableState
import compose.icons.MaterialDesignIcons
import compose.icons.materialdesignicons.Refresh

enum class SketchableThemeType {
    System,Light,Dark
}

@Composable
fun EditorSettingsDialog(
    onDismissRequest: () -> Unit,
    state: SketchableState,
    themeType: SketchableThemeType,
    onUpdateThemeType: (SketchableThemeType) -> Unit,
) {
    DialogBox(
        modifier = Modifier.padding(vertical = 16.dp, horizontal = 8.dp),
        onDismissRequest = onDismissRequest
    ) {
        Column(modifier = Modifier.verticalScroll(rememberScrollState())) {
            Labeled("Interface") {
                ThemeTypeSwitch(
                    themeType = themeType,
                    onUpdate = onUpdateThemeType
                )
                BackgroundSelector(
                    state = state,
                )
            }
            SketchZoomSettings(state)
        }
    }
}

@Composable
fun SketchZoomSettings(state: SketchableState) {
    Labeled("Zoom , Pan & Rotate", rowContent = {
        TextButton(
            onClick = {
                state.zoomableState.reset()
                state.mode = PointerMode.Draw
            }
        ) {
            Text(text = "Reset")
        }
    }) {
        MultiFingerZoomSetting(state)
        RotationEnabledSetting(state)
    }
}

@Composable
fun InterfaceSettings(
    themeType: SketchableThemeType,
    onUpdateThemeType: (SketchableThemeType) -> Unit,
    currentBackground: Color,
    onUpdateBackground: (Color) -> Unit,
) {
    Labeled("Interface") {
        ThemeTypeSwitch(
            themeType = themeType,
            onUpdate = onUpdateThemeType
        )
        BackgroundSelector(
            currentBackground = currentBackground,
            onUpdateBackground = onUpdateBackground
        )
    }
}

@Composable
fun MultiFingerZoomSetting(state: SketchableState) {
    Row(
        modifier = Modifier
            .clickable {
                state.zoomableState.multiFingerZoom = !state.zoomableState.multiFingerZoom
            }
            .fillMaxWidth()
            .padding(vertical = 8.dp), verticalAlignment = Alignment.CenterVertically
    ) {
        Text(
            modifier = Modifier.weight(1f),
            text = "Multi Finger Zoom",
            style = MaterialTheme.typography.labelMedium,
            color = MaterialTheme.colorScheme.onBackground
        )
        Switch(
            checked = state.zoomableState.multiFingerZoom,
            onCheckedChange = {
                state.zoomableState.multiFingerZoom = it
                if (it) {
                    state.mode = PointerMode.Draw
                }
            }
        )
    }
}

@Composable
fun RotationEnabledSetting(state: SketchableState) {
    Row(
        modifier = Modifier
            .clickable {
                state.zoomableState.rotateEnabled = !state.zoomableState.rotateEnabled
            }
            .fillMaxWidth()
            .padding(vertical = 8.dp), verticalAlignment = Alignment.CenterVertically
    ) {
        Text(
            modifier = Modifier.weight(1f),
            text = "Rotation Enabled",
            style = MaterialTheme.typography.labelMedium,
            color = MaterialTheme.colorScheme.onBackground
        )
        Switch(
            checked = state.zoomableState.rotateEnabled,
            onCheckedChange = {
                state.zoomableState.rotateEnabled = it
                if (!it) {
                    state.zoomableState.angle = 0f
                }
            }
        )
    }
}

@Composable
fun ThemeTypeSwitch(
    themeType: SketchableThemeType,
    onUpdate: (SketchableThemeType) -> Unit
) {
    val isSystemInDark = isSystemInDarkTheme()
    Row(verticalAlignment = Alignment.CenterVertically) {
        Text(
            modifier = Modifier.weight(1f),
            text = "Dark Theme",
            color = MaterialTheme.colorScheme.onBackground,
            style = MaterialTheme.typography.labelMedium
        )
        Switch(
            checked = themeType == SketchableThemeType.Dark || (themeType == SketchableThemeType.System && isSystemInDark),
            onCheckedChange = {
                val newThemeType = if (it) {
                    if (isSystemInDark) {
                        SketchableThemeType.System
                    } else {
                        SketchableThemeType.Dark
                    }
                } else {
                    if (isSystemInDark) {
                        SketchableThemeType.Light
                    } else {
                        SketchableThemeType.System
                    }
                }
                onUpdate(newThemeType)
            }
        )
    }
}

@Composable
fun BackgroundSelector(
    state: SketchableState
) = BackgroundSelector(
    currentBackground = state.backgroundColor,
    onUpdateBackground = { state.backgroundColor = it }
)

@Composable
fun BackgroundSelector(
    currentBackground: Color,
    onUpdateBackground: (Color) -> Unit
) {
    var backgroundPickerDialog by remember { mutableStateOf(false) }
    Row(verticalAlignment = Alignment.CenterVertically) {
        Text(
            text = "Background",
            color = MaterialTheme.colorScheme.onBackground,
            style = MaterialTheme.typography.labelMedium
        )
        Spacer(modifier = Modifier.width(16.dp))
        IconButton(onClick = { onUpdateBackground(Color.Transparent) }) {
            Icon(
                imageVector = MaterialDesignIcons.Refresh,
                contentDescription = null
            )
        }
        Spacer(Modifier.weight(1f))
        Box(
            modifier = Modifier
                .padding(vertical = 16.dp)
                .size(28.dp)
                .border(2.dp, color = MaterialTheme.colorScheme.onBackground)
                .background(color = currentBackground)
                .clickable(onClick = { backgroundPickerDialog = true })
        )
    }
    if (backgroundPickerDialog) {
        DialogBox(onDismissRequest = { backgroundPickerDialog = false }) {
            ClassicColorPicker(
                modifier = Modifier
                    .padding(36.dp)
                    .widthIn(max = 460.dp)
                    .heightIn(max = 400.dp),
                color = HsvColor.from(Color.Red),
                onColorChanged = { c: HsvColor ->
                    onUpdateBackground(c.toColor())
                }
            )
        }
    }
}

@OptIn(ExperimentalUnitApi::class)
@Composable
private fun Labeled(
    text: String,
    rowContent: @Composable () -> Unit = {},
    content: @Composable () -> Unit,
) {
    Column(modifier = Modifier.padding(vertical = 8.dp, horizontal = 16.dp)) {
        Row(verticalAlignment = Alignment.CenterVertically) {
            Text(
                modifier = Modifier.weight(1f),
                text = text,
                style = MaterialTheme.typography.labelLarge,
                color = MaterialTheme.colorScheme.onBackground.copy(.6f),
                fontSize = TextUnit(13F, TextUnitType.Sp)
            )
            rowContent()
        }
        content()
    }
}