package com.wakaztahir.sketchable.canvas

import androidx.compose.ui.graphics.Color
import androidx.compose.ui.graphics.drawscope.DrawScope
import com.wakaztahir.sketchable.model.SketchableState

fun DrawScope.drawSketch(state: SketchableState) {

    // Setting up canvas for state
    trackViewportSize(state)

    if(state.backgroundColor.value != Color.Transparent.value){
        drawRect(
            color = state.backgroundColor,
            size = size
        )
    }

    // Drawing layers of the canvas
    for (layer in state.layers) {
        if (layer.isVisible) {
            with(layer) {
                draw(inPreview = true)
            }
        }
    }

    // Drawing the current brush layer
    with(state.brush) {
        draw(state)
    }
}