package com.wakaztahir.sketchable.layers

import androidx.compose.runtime.getValue
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.setValue
import androidx.compose.ui.geometry.Offset
import androidx.compose.ui.graphics.ImageBitmap
import androidx.compose.ui.graphics.Paint
import androidx.compose.ui.graphics.drawscope.DrawScope
import androidx.compose.ui.graphics.vector.ImageVector
import androidx.compose.ui.unit.IntOffset
import androidx.compose.ui.unit.IntSize
import com.wakaztahir.sketchable.model.*
import com.wakaztahir.sketchable.serializer.SerializationHelper
import com.wakaztahir.sketchable.utils.convertPathToImageBitmap
import compose.icons.MaterialDesignIcons
import compose.icons.materialdesignicons.Image
import kotlinx.serialization.SerialName

class ImageLayer(
    val imageBitmap: ImageBitmap,
    val path: String,
    override var name: String = "Image Layer",
    dstSize: IntSize = IntSize(width = imageBitmap.width, height = imageBitmap.height),
    dstOffset: IntOffset = IntOffset.Zero
) : SketchableLayer, Movable, Resizable {

    companion object {
        val paint = Paint()
    }

    @SerialName("ImageLayer")
    @kotlinx.serialization.Serializable
    data class Saved(
        val path: String,
        val name: String,
        val dstWidth: Int,
        val dstHeight: Int,
        val dstX: Int,
        val dstY: Int,
    ) : SerializableLayer {

        private fun getImageBitmap(): ImageBitmap {
            if (path.isEmpty()) error("saved path cannot be empty")
            return convertPathToImageBitmap(path)
        }

        override fun toLayer(offset: Offset, helper: SerializationHelper) = ImageLayer(
            imageBitmap = getImageBitmap(),
            name = name,
            path = path,
            dstSize = IntSize(dstWidth, dstHeight),
            dstOffset = IntOffset((dstX + offset.x).toInt(), (dstY + offset.y).toInt())
        )

        override fun updateBounds(bounds: SketchableBounds, helper: SerializationHelper) {
            with(bounds) {
                left = minOf(dstX.toFloat(), left)
                top = minOf(dstY.toFloat(), top)
                right = maxOf(dstX + dstWidth.toFloat(), right)
                bottom = maxOf(dstY + dstHeight.toFloat(), bottom)
            }
        }
    }

    override fun toSerializableLayer(offset: Offset) = Saved(
        path = path,
        name = this.name,
        dstWidth = this.dstSize.width,
        dstHeight = this.dstSize.height,
        dstX = (this.dstOffset.x + offset.x).toInt(),
        dstY = (this.dstOffset.y + offset.y).toInt()
    )

    override fun applyOffset(offset: Offset) {
        dstOffset = IntOffset(
            x = (dstOffset.x + offset.x).toInt(),
            y = (dstOffset.y + offset.y).toInt()
        )
    }

    override fun icon(): ImageVector = MaterialDesignIcons.Image

    override var isLocked: Boolean = false
    override var isVisible: Boolean = true

    private var dstSize by mutableStateOf(dstSize)
    private var dstOffset by mutableStateOf(dstOffset)

    override var width: Int
        get() = dstSize.width
        set(value) {
            dstSize = IntSize(value, dstSize.height)
        }

    override var height: Int
        get() = dstSize.height
        set(value) {
            dstSize = IntSize(dstSize.width, value)
        }

    override var x: Float
        get() = dstOffset.x.toFloat()
        set(value) {
            dstOffset = IntOffset(value.toInt(), dstOffset.y)
        }

    override var y: Float
        get() = dstOffset.y.toFloat()
        set(value) {
            dstOffset = IntOffset(dstOffset.x, value.toInt())
        }

    private val srcSize = IntSize(width = imageBitmap.width, imageBitmap.height)
    private val srcOffset = IntOffset.Zero

    override fun DrawScope.draw(inPreview: Boolean) {
        drawContext.canvas.drawImageRect(
            image = imageBitmap,
            srcSize = srcSize,
            srcOffset = srcOffset,
            dstSize = dstSize,
            dstOffset = dstOffset,
            paint = paint
        )
    }
}