package com.wakaztahir.sketchable.model

import androidx.compose.ui.geometry.Offset
import androidx.compose.ui.text.ExperimentalTextApi
import androidx.compose.ui.text.TextMeasurer
import com.wakaztahir.qawazlogger.logIt
import com.wakaztahir.sketchable.serializer.SerializationHelper
import kotlinx.coroutines.Dispatchers
import kotlinx.coroutines.withContext

interface SketchableDrawing {

    val viewportWidth: Float
    val viewportHeight: Float

    val layers: List<SketchableLayer>

    fun updateViewportSize(width: Float, height: Float)

    fun offsetBy(newOffset: Offset) {
        for (layer in layers) {
            layer.applyOffset(newOffset)
        }
    }

    fun getSerializableLayers(): MutableList<SerializableLayer> {
        val layers = mutableListOf<SerializableLayer>()
        val offset = Offset(
            x = -viewportWidth / 2f,
            y = -viewportHeight / 2f
        )
        for (layer in this.layers) {
            try {
                layers.add(layer.toSerializableLayer(offset))
            } catch (e: Exception) {
                e.logIt(message = "failed to convert layer to serializable")
            }
        }
        return layers
    }

    @OptIn(ExperimentalTextApi::class)
    fun convertSerializableLayers(
        textMeasurer: TextMeasurer,
        layers: List<SerializableLayer>
    ): MutableList<SketchableLayer> {
        val newLayers = mutableListOf<SketchableLayer>()
        val offset = Offset(
            x = viewportWidth / 2f,
            y = viewportHeight / 2f
        )
        val serializationHelper = SerializationHelper(textMeasurer)
        for (layer in layers) {
            try {
                newLayers.add(layer.toLayer(offset, serializationHelper))
            } catch (e: Exception) {
                e.logIt(message = "error while loading layer , probably image")
            }
        }
        return newLayers
    }

}