package com.wakaztahir.sketchable.serializer

import androidx.compose.ui.graphics.Color
import com.wakaztahir.sketchable.model.SerializableLayer
import kotlinx.serialization.SerialName
import kotlinx.serialization.Serializable

@SerialName("SketchableState")
@Serializable
class SerializedState(
    val layers: List<SerializableLayer>,
    @Serializable(with = ColorSerializer::class)
    val backgroundColor: Color = Color.Transparent
){
    fun isEmpty() = layers.isEmpty()

    companion object {
        val Empty get() = SerializedState(listOf())
    }

}