package com.wakaztahir.sketchable.utils

import androidx.compose.runtime.*
import com.wakaztahir.qawazlogger.logIt
import com.wakaztahir.sketchable.Res
import dev.icerock.moko.resources.StringResource
import dev.icerock.moko.resources.provider.JsStringProvider

private var loader: JsStringProvider? = null
private var isLoading by mutableStateOf(false)

@Composable
internal actual fun stringResource(id: StringResource): String {
    return if (loader == null && !isLoading) {
        isLoading = true
        LaunchedEffect(null) {
            try {
                loader = Res.stringsLoader.getOrLoad()
            } catch (e: Throwable) {
                e.logIt(report = true)
            }
            isLoading = false
        }
        ""
    } else {
        return loader?.let { load ->
            try {
                id.localized(load, null)
            } catch (e: Throwable) {
                e.logIt(report = true)
                null
            }
        } ?: "fail"
    }
}