package com.wakaztahir.sketchable.model

import kotlinx.serialization.SerialName

@kotlinx.serialization.Serializable
abstract class PathOperation {
    abstract fun clone() : PathOperation
}

//Path Operations
@SerialName("Move")
@kotlinx.serialization.Serializable
data class MoveToOp(var x: Float, var y: Float) : PathOperation() {
    override fun clone(): PathOperation = copy()
}

@SerialName("Line")
@kotlinx.serialization.Serializable
data class LineToOp(var x: Float, var y: Float) : PathOperation(){
    override fun clone(): PathOperation = copy()
}

@SerialName("Quad")
@kotlinx.serialization.Serializable
data class QuadToOp(var x1: Float, var y1: Float, var x2: Float, var y2: Float) : PathOperation(){
    override fun clone(): PathOperation = copy()
}