package com.wakaztahir.sketchable.components.tools.brushes

import androidx.compose.foundation.background
import androidx.compose.foundation.layout.*
import androidx.compose.foundation.shape.RoundedCornerShape
import androidx.compose.material3.MaterialTheme
import androidx.compose.material3.Slider
import androidx.compose.material3.Text
import androidx.compose.runtime.*
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.draw.clip
import androidx.compose.ui.platform.LocalDensity
import androidx.compose.ui.unit.dp
import com.wakaztahir.sketchable.brushes.RoundBrush

@Composable
internal fun RoundBrushOptions(modifier: Modifier = Modifier, brush: RoundBrush) {
    val density = LocalDensity.current
    val maxBrushSize = 80f
    var brushSize by remember(brush.size){ mutableStateOf(brush.size) }
    Column(modifier = modifier) {
        Row(
            modifier = modifier,
            horizontalArrangement = Arrangement.Center,
            verticalAlignment = Alignment.CenterVertically
        ) {
            Text(
                text = "Size",
                color = MaterialTheme.colorScheme.onBackground,
                style = MaterialTheme.typography.labelMedium
            )
            Slider(
                modifier = Modifier.weight(1f),
                value = brushSize,
                valueRange = 0f..80f,
                onValueChange = {
                    brushSize = it
                    brush.size = it
                }
            )
            Box(
                Modifier
                    .padding(start = 12.dp, end = 12.dp)
                    .size(with(density) { maxBrushSize.toDp() })
            ) {
                Box(
                    modifier = Modifier
                        .align(Alignment.Center)
                        .size(with(density) { brushSize.toDp() })
                        .clip(RoundedCornerShape(50))
                        .background(color = MaterialTheme.colorScheme.onBackground),
                )
            }
        }
    }
}