@Composable
fun PaintBottomBar(
    sketchableState: SketchableState,
    tools: SketchableToolsState,
    onAddImageClick: () -> Unit,
) {

    fun toggleTools(panelState: ToolsPanelState) {
        tools.panelState = if (tools.panelState == panelState) {
            ToolsPanelState.Hidden
        } else {
            panelState
        }
    }

    var addTextDialog by remember { mutableStateOf(false) }
    var addMenu by remember { mutableStateOf(false) }
    val unselectedColor = MaterialTheme.colorScheme.onBackground.copy(.7f)

    fun toggleAddMenu() {
        addMenu = !addMenu
    }

    ToolsBottomBar(
        containerColor = MaterialTheme.colorScheme.surfaceColorAtElevation(3.dp).copy(.8f)
    ) {
        Column {
            IconButton(onClick = { addMenu = true }) {
                Icon(
                    imageVector = MaterialDesignIcons.PlusCircleOutline,
                    contentDescription = null,
                    tint = MaterialTheme.colorScheme.onBackground.copy(.7f)
                )
            }
            DropdownMenu(
                expanded = addMenu,
                onDismissRequest = { addMenu = false },
            ) {
                DropdownMenuItem(
                    onClick = { toggleAddMenu().also { addTextDialog = true } },
                    leadingIcon = {
                        Icon(
                            imageVector = MaterialDesignIcons.FormatTextVariant,
                            contentDescription = "Text Icon"
                        )
                    },
                    text = {
                        Text(text = "Text")
                    }
                )
                DropdownMenuItem(
                    modifier = Modifier.widthIn(min = 160.dp),
                    onClick = onAddImageClick,
                    leadingIcon = {
                        Icon(
                            imageVector = MaterialDesignIcons.Image,
                            contentDescription = "Image Icon"
                        )
                    },
                    text = {
                        Text(text = "Image")
                    }
                )
            }
        }
        IconButton(onClick = {
            if (sketchableState.brush is EraserBrush) {
                sketchableState.brush = tools.lastBrush ?: RoundBrush()
            } else {
                toggleTools(ToolsPanelState.BrushPanel)
            }
        }) {
            Icon(
                imageVector = MaterialDesignIcons.PencilOutline,
                contentDescription = "Pen Icon",
                tint = if (sketchableState.brush is EraserBrush) {
                    unselectedColor
                } else {
                    MaterialTheme.colorScheme.primary
                }
            )
        }
        IconButton(onClick = { toggleTools(ToolsPanelState.ColorsPanel) }) {
            Icon(
                imageVector = MaterialDesignIcons.Palette,
                contentDescription = "Color Icon",
                tint = unselectedColor
            )
        }
        val eraserRing = MaterialTheme.colorScheme.onBackground
        IconButton(onClick = {
            if (sketchableState.brush is EraserBrush) {
                toggleTools(ToolsPanelState.BrushPanel)
            } else {
                tools.lastBrush = sketchableState.brush
                sketchableState.brush = EraserBrush(
                    ringColor = eraserRing
                ).also {
                    try {
                        if (tools.lastBrush is RoundBrush) {
                            it.size = (tools.lastBrush!! as RoundBrush).size
                        }
                    } catch (e: Exception) {
                        e.logIt()
                    }
                }
                if (tools.panelState != ToolsPanelState.Hidden && tools.panelState != ToolsPanelState.BrushPanel) {
                    toggleTools(ToolsPanelState.BrushPanel)
                }
            }
        }) {
            Icon(
                imageVector = MaterialDesignIcons.Eraser,
                contentDescription = "Eraser Icon",
                tint = if (sketchableState.brush is EraserBrush) {
                    MaterialTheme.colorScheme.primary
                } else {
                    unselectedColor
                }
            )
        }
    }

    if (addTextDialog) {
        AddTextDialog(
            onDismissRequest = { addTextDialog = false },
            state = sketchableState,
        )
    }

}

@Composable
private fun ToolsBottomBar(
    modifier: Modifier = Modifier,
    containerColor: Color = BottomAppBarDefaults.containerColor,
    contentColor: Color = contentColorFor(containerColor),
    tonalElevation: Dp = BottomAppBarDefaults.ContainerElevation,
    contentPadding: PaddingValues = BottomAppBarDefaults.ContentPadding,
    windowInsets: WindowInsets = BottomAppBarDefaults.windowInsets,
    maxHeight: Dp = 80.0.dp,
    content: @Composable RowScope.() -> Unit
) {
    Surface(
        color = containerColor,
        contentColor = contentColor,
        tonalElevation = tonalElevation,
        shape = RectangleShape,
        modifier = modifier
    ) {
        Row(
            Modifier
                .fillMaxWidth()
                .windowInsetsPadding(windowInsets)
                .heightIn(min = 80.0.dp, max = maxHeight)
                .padding(contentPadding),
            horizontalArrangement = Arrangement.Center,
            verticalAlignment = Alignment.CenterVertically,
            content = content
        )
    }
}


@OptIn(ExperimentalMaterial3Api::class, ExperimentalTextApi::class)
@Composable
private fun AddTextDialog(
    state: SketchableState,
    onDismissRequest: () -> Unit
) {
    val textMeasurer = rememberTextMeasurer()
    var text by remember { mutableStateOf("") }
    val focusRequester = remember { FocusRequester() }
    AlertDialog(
        onDismissRequest = onDismissRequest,
        title = {
            Text(text = "Add Text")
        },
        text = {
            OutlinedTextField(
                modifier = Modifier.focusRequester(focusRequester),
                value = text,
                onValueChange = {
                    text = it
                },
                label = {
                    Text(text = "Text")
                },
                placeholder = {
                    Text(text = "Text to add")
                },
                colors = TextFieldDefaults.outlinedTextFieldColors(
                    textColor = MaterialTheme.colorScheme.onBackground,
                    focusedBorderColor = Color.Transparent,
                    unfocusedBorderColor = Color.Transparent
                )
            )
        },
        confirmButton = {
            TextButton(onClick = {
                val layer = TextLayer(text, textMeasurer = textMeasurer)
                state.selectLayer(layer)
                state.addLayerWithUndo(layer)
                onDismissRequest()
            }) {
                Text(text = stringResource(Res.strings.confirm))
            }
        }
    )
    LaunchedEffect(null) {
        try {
            focusRequester.requestFocus()
        } catch (e: Exception) {
            e.logIt(report = false)
        }
    }
}