package com.wakaztahir.sketchable.serializer

import androidx.compose.ui.graphics.Color
import kotlinx.serialization.KSerializer
import kotlinx.serialization.SerialName
import kotlinx.serialization.descriptors.SerialDescriptor
import kotlinx.serialization.encoding.Decoder
import kotlinx.serialization.encoding.Encoder
import kotlinx.serialization.serializer

@SerialName("Color")
@kotlinx.serialization.Serializable
private class ColorSurrogate(
    val r: Float,
    val g: Float,
    val b: Float,
    val a: Float
)

object ColorSerializer : KSerializer<Color> {

    private val kSerializer = serializer<ColorSurrogate>()

    override val descriptor: SerialDescriptor
        get() = kSerializer.descriptor


    override fun deserialize(decoder: Decoder): Color {
        val color = decoder.decodeSerializableValue(kSerializer)
        return Color(red = color.r, green = color.g, blue = color.b, alpha = color.a)
    }

    override fun serialize(encoder: Encoder, value: Color) {
        encoder.encodeSerializableValue(
            kSerializer,
            ColorSurrogate(r = value.red, g = value.green, b = value.blue, a = value.alpha)
        )
    }

}