package com.wakaztahir.sketchable.model

import androidx.compose.ui.geometry.Offset
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.graphics.drawscope.DrawScope
import androidx.compose.ui.graphics.vector.ImageVector
import com.wakaztahir.sketchable.serializer.SerializationHelper

/**
 * Anything that draws on canvas & can handle input events
 */
interface Sketchable {
    fun DrawScope.draw(inPreview: Boolean)
}

interface SketchableExportableLayer {
    fun svgString(): String
}

interface SerializableLayer {

    fun toLayer(offset: Offset,helper : SerializationHelper) : SketchableLayer

    fun updateBounds(bounds : SketchableBounds,helper: SerializationHelper)

}

/**
 * Anything that can be considered a layer
 */
interface SketchableLayer : Sketchable {

    var name: String
    var isLocked: Boolean
    var isVisible: Boolean

    fun getColor(): Color? = null

    fun icon(): ImageVector

    fun toSerializableLayer(offset: Offset) : SerializableLayer

    fun applyOffset(offset: Offset)

}