package com.wakaztahir.sketchable.layers

import androidx.compose.runtime.getValue
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.setValue
import androidx.compose.ui.geometry.Offset
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.graphics.drawscope.DrawScope
import androidx.compose.ui.graphics.drawscope.withTransform
import androidx.compose.ui.graphics.vector.ImageVector
import androidx.compose.ui.text.*
import com.wakaztahir.sketchable.model.*
import com.wakaztahir.sketchable.serializer.ColorSerializer
import com.wakaztahir.sketchable.serializer.SerializationHelper
import compose.icons.MaterialDesignIcons
import compose.icons.materialdesignicons.FormatTextVariant
import kotlinx.serialization.SerialName

class TextLayer @OptIn(ExperimentalTextApi::class) constructor(
    text: String,
    val textMeasurer : TextMeasurer,
    override var textSize: Float = 14f,
    override val color: Color = Color.Black,
    override val isBold: Boolean = false,
    override val isItalic: Boolean = false,
    x: Float = 200f,
    y: Float = 200f,
) : SketchableLayer, Movable, Resizable,TextPresenter {

    @SerialName("TextLayer")
    @kotlinx.serialization.Serializable
    class Saved constructor(
        override val text: String,
        override val textSize: Float,
        @kotlinx.serialization.Serializable(with = ColorSerializer::class)
        override val color: Color,
        override val isBold: Boolean,
        override val isItalic: Boolean,
        val x: Float,
        val y: Float,
    ) : SerializableLayer,TextPresenter {
        @OptIn(ExperimentalTextApi::class)
        override fun toLayer(offset: Offset, helper: SerializationHelper) = TextLayer(
            text = this.text,
            textSize = this.textSize,
            color = this.color,
            isBold = this.isBold,
            isItalic = this.isItalic,
            x = this.x + offset.x,
            y = this.y + offset.y,
            textMeasurer = helper.textMeasurer
        )

        @OptIn(ExperimentalTextApi::class)
        override fun updateBounds(bounds: SketchableBounds, helper: SerializationHelper) {
            val size = measure(helper.textMeasurer).size
            with(bounds) {
                left = minOf(x, left)
                top = minOf(y, top)
                right = maxOf(x + size.width, right)
                bottom = maxOf(y + size.height, bottom)
            }
        }
    }

    override fun toSerializableLayer(offset: Offset) = Saved(
        text = this.text,
        textSize = this.textSize,
        color = this.color,
        isBold = this.isBold,
        isItalic = this.isItalic,
        x = this.x + offset.x,
        y = this.y + offset.y
    )

    override var text by mutableStateOf(text)
        private set

    override var name: String = text.substring(0, minOf(20, text.length))
    override var isLocked: Boolean = false
    override var isVisible: Boolean = true

    override var x by mutableStateOf(x)
    override var y by mutableStateOf(y)


    @OptIn(ExperimentalTextApi::class)
    private var measurement: TextLayoutResult by mutableStateOf(measure(textMeasurer))

    private var scaleX by mutableStateOf(1f)
    private var scaleY by mutableStateOf(1f)

    override var width: Int
        get() = measurement.size.width
        set(value) {
            scaleX = value.toFloat() / measurement.size.width.toFloat()
        }

    override var height: Int
        get() = measurement.size.height
        set(value) {
            scaleY = value.toFloat() / measurement.size.height.toFloat()
        }

    override fun icon(): ImageVector = MaterialDesignIcons.FormatTextVariant

    override fun applyOffset(offset: Offset) {
        x += offset.x
        y += offset.y
    }

    @OptIn(ExperimentalTextApi::class)
    override fun DrawScope.draw(inPreview: Boolean) {
        withTransform({
            translate(x,y)
            scale(scaleX,scaleY)
        }){
            drawText(
                textLayoutResult = measurement
            )
        }
    }
}