package com.wakaztahir.sketchable.components.overlays

import androidx.compose.foundation.background
import androidx.compose.foundation.gestures.detectTransformGestures
import androidx.compose.foundation.layout.Box
import androidx.compose.foundation.layout.BoxWithConstraints
import androidx.compose.foundation.layout.offset
import androidx.compose.foundation.layout.size
import androidx.compose.foundation.shape.CircleShape
import androidx.compose.material3.Icon
import androidx.compose.runtime.Composable
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.draw.drawBehind
import androidx.compose.ui.geometry.Offset
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.input.pointer.pointerInput
import androidx.compose.ui.platform.LocalDensity
import androidx.compose.ui.unit.DpSize
import androidx.compose.ui.unit.IntOffset
import androidx.compose.ui.unit.dp
import com.wakaztahir.sketchable.canvas.getTransformedPositionInverse
import com.wakaztahir.sketchable.model.Movable
import com.wakaztahir.sketchable.model.Resizable
import com.wakaztahir.sketchable.model.SketchableLayer
import com.wakaztahir.sketchable.model.SketchableState
import compose.icons.MaterialDesignIcons
import compose.icons.materialdesignicons.CursorMove
import compose.icons.materialdesignicons.MoveResizeVariant

@Composable
fun TransformableOverlay(
    modifier: Modifier,
    state: SketchableState,
    layer: SketchableLayer,
    overlayState: ComposableOverlayState,
    onDismissRequest: () -> Unit
) {
    BoxWithConstraints(modifier = modifier) {

        val density = LocalDensity.current

        val totalWidth = with(density) { maxWidth.toPx() }
        val totalHeight = with(density) { maxHeight.toPx() }

        if (layer is Movable) {

            val position = Offset(layer.x, layer.y).getTransformedPositionInverse(
                state.zoomableState,
                totalWidth,
                totalHeight
            )
            val endPosition =
                Offset(layer.x + layer.width, layer.y + layer.height).getTransformedPositionInverse(
                    state.zoomableState,
                    totalWidth,
                    totalHeight
                )

            val movePosition = IntOffset(x = position.x.toInt(), position.y.toInt())

            val moveSize = DpSize(
                width = with(density) { (endPosition.x - position.x).toDp() },
                height = with(density) { (endPosition.x - position.x).toDp() }
            )

            Box(
                modifier = Modifier
                    .matchParentSize()
                    .then(
                        Modifier.drawMovableGuides(
                            position = position,
                            endPosition = endPosition,
                            color = overlayState.guidesColor
                        )
                    )
            ) {
                Box(
                    modifier = Modifier
                        .size(moveSize)
                        .offset { movePosition },
                ) {
                    Icon(
                        modifier = Modifier
                            .align(Alignment.TopEnd)
                            .size(32.dp)
                            .background(
                                color = overlayState.backgroundColor.copy(.6f),
                                shape = CircleShape
                            )
                            .moveInput(state, layer),
                        imageVector = MaterialDesignIcons.CursorMove,
                        tint = overlayState.contentColor,
                        contentDescription = null
                    )

                    if (layer is Resizable) {
                        // Scale Input
                        Icon(
                            modifier = Modifier
                                .align(Alignment.BottomEnd)
                                .size(32.dp)
                                .background(
                                    color = overlayState.backgroundColor.copy(.6f),
                                    shape = CircleShape
                                )
                                .scaleInput(layer),
                            imageVector = MaterialDesignIcons.MoveResizeVariant,
                            tint = overlayState.contentColor,
                            contentDescription = null
                        )
                    }
                }
            }
        }
    }
}

private fun Modifier.moveInput(
    state: SketchableState,
    layer: Movable,
) = this.pointerInput(layer) {
    detectTransformGestures { centroid, pan, zoom, rotation ->
        layer.x += pan.x / state.zoomableState.scale
        layer.y += pan.y / state.zoomableState.scale
    }
}

private fun Modifier.scaleInput(
    layer: Resizable,
    minWidth: Int = 10,
    minHeight: Int = 10,
) = this.pointerInput(layer) {
    detectTransformGestures { centroid, pan, zoom, rotation ->
        if (layer.width + pan.x > minWidth) {
            layer.width += pan.x.toInt()
        }
        if (layer.height + pan.y > minHeight) {
            layer.height += pan.y.toInt()
        }
    }
}


private fun Modifier.drawMovableGuides(
    position: Offset,
    endPosition: Offset,
    color: Color
) = this.drawBehind {

    // drawing top horizontal guide
    drawLine(
        color = color,
        start = Offset(x = 0f, y = position.y),
        end = Offset(x = this.size.width, y = position.y)
    )
    // drawing bottom horizontal guide
    drawLine(
        color = color,
        start = Offset(x = 0f, y = endPosition.y),
        end = Offset(x = this.size.width, y = endPosition.y)
    )
    // drawing start vertical guide
    drawLine(
        color = color,
        start = Offset(x = position.x, y = 0f),
        end = Offset(x = position.x, y = this.size.height)
    )
    // drawing end vertical guide
    drawLine(
        color = color,
        start = Offset(x = endPosition.x, y = 0f),
        end = Offset(x = endPosition.x, y = this.size.height)
    )
}