package com.wakaztahir.sketchapp

import com.wakaztahir.qawazlogger.LogType
import com.wakaztahir.qawazlogger.logIt
import kotlinx.browser.document
import org.w3c.dom.HTMLAnchorElement
import org.w3c.dom.HTMLInputElement
import org.w3c.dom.url.URL
import org.w3c.files.*

fun loadFileUsingSelectDialog(onLoaded: (content: File) -> Unit) {
    val input = document.createElement("input") as HTMLInputElement
    input.type = "file"
    input.onchange = { event ->
        val files = (event.target as? HTMLInputElement)?.files
        if (files != null && files.length > 0) {
            val file = files[0]
            if (file != null) {
                onLoaded(file)
            }
        }
    }
    input.click()
}

fun loadFileContentUsingSelectDialog(onLoaded: (content: dynamic) -> Unit) {
    loadFileUsingSelectDialog { file ->
        val reader = FileReader()
        reader.readAsText(file, "UTF-8")
        reader.onload = { readerEvent ->
            onLoaded((readerEvent.target as FileReader).result)
        }
    }
}

fun downloadFileWithUrl(fileName: String, urlFile: String) {
    val a = document.createElement("a") as HTMLAnchorElement
    a.style.display = "none"
    if (document.body != null) {
        document.body?.appendChild(a)
        a.href = urlFile
        a.download = fileName
        a.click()
        a.remove()
    } else {
        Throwable("body doesn't exist").logIt(type = LogType.WTF)
    }
}

fun downloadFileWithContent(fileName: String, type: String, content: String) {
    val blobData = Blob(arrayOf(content), BlobPropertyBag(type = type))
    val url = URL.createObjectURL(blobData)
    downloadFileWithUrl(fileName, url)
    URL.revokeObjectURL(url)
}

