package com.wakaztahir.sketchable.serializer

import androidx.compose.ui.graphics.Color
import com.wakaztahir.sketchable.layers.ColoredPath
import com.wakaztahir.sketchable.layers.ImageLayer
import com.wakaztahir.sketchable.layers.TextLayer
import com.wakaztahir.sketchable.model.*
import kotlinx.serialization.decodeFromString
import kotlinx.serialization.encodeToString
import kotlinx.serialization.json.Json
import kotlinx.serialization.modules.SerializersModule
import kotlinx.serialization.modules.polymorphic
import kotlinx.serialization.modules.subclass

object SketchableSerializer {

    val serializer = Json {
        serializersModule = SerializersModule {
            polymorphic(PathOperation::class) {
                subclass(MoveToOp::class)
                subclass(LineToOp::class)
                subclass(QuadToOp::class)
            }
            polymorphic(SerializableLayer::class) {
                subclass(ColoredPath.Saved::class)
                subclass(ImageLayer.Saved::class)
                subclass(TextLayer.Saved::class)
            }
        }
        ignoreUnknownKeys = true
        encodeDefaults = true
    }


    fun serialize(layers: List<SerializableLayer>,backgroundColor: Color = Color.Transparent): String {
        val surrogate = SerializedState(layers = layers,backgroundColor = backgroundColor)
        return serializer.encodeToString(surrogate)
    }

    fun serialize(state : SerializedState) : String {
        return serializer.encodeToString(state)
    }

    @Deprecated("use deserialize(json : String)")
    fun deserializeToLayers(json: String): List<SerializableLayer> {
        return serializer.decodeFromString<SerializedState>(json).layers
    }

    fun deserialize(json : String) : SerializedState {
        return serializer.decodeFromString(json)
    }



}