package com.wakaztahir.helpers

import androidx.compose.foundation.layout.*
import androidx.compose.material3.*
import androidx.compose.runtime.Composable
import androidx.compose.runtime.CompositionLocalProvider
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.graphics.Shape
import androidx.compose.ui.graphics.isUnspecified
import androidx.compose.ui.unit.Dp
import androidx.compose.ui.unit.dp


private fun Color.fix(color: Color): Color {
    return if (this.isUnspecified) {
        color
    } else {
        this
    }
}

@Composable
actual fun AlertDialog(
    onDismissRequest: () -> Unit,
    confirmButton: @Composable () -> Unit,
    modifier: Modifier,
    dismissButton: @Composable (() -> Unit)?,
    icon: @Composable (() -> Unit)?,
    title: @Composable (() -> Unit)?,
    text: @Composable (() -> Unit)?,
) {
    DialogBox(modifier = Modifier.padding(16.dp),onDismissRequest = onDismissRequest) {
        Column(verticalArrangement = Arrangement.spacedBy(8.dp)) {
            Row(horizontalArrangement = Arrangement.spacedBy(8.dp)) {
                icon?.invoke()
                title?.invoke()
            }
            text?.invoke()
            Row(modifier = Modifier.align(Alignment.End)) {
                dismissButton?.let { dismissButton2 ->
                    dismissButton2()
                    Spacer(modifier = Modifier.width(8.dp))
                }
                confirmButton.invoke()
            }
        }
    }
}