package com.wakaztahir.qawazlogger

sealed interface LogType {

    val name: String
    fun logPlatform(tag: String, message: String, exception: Throwable?)

    fun logQawaz(tag: String, message: String, exception: Throwable?)

    object Debug : LogType {
        override val name: String = "DEBUG"
        override fun logPlatform(tag: String, message: String, exception: Throwable?) {
            PlatformLogger.logDebug(tag, message, exception)
        }

        override fun logQawaz(tag: String, message: String, exception: Throwable?) {
            QLog.logDebug(tag, message, exception)
        }
    }

    object Error : LogType {
        override val name: String = "ERROR"
        override fun logPlatform(tag: String, message: String, exception: Throwable?) {
            PlatformLogger.logError(tag, message, exception)
        }

        override fun logQawaz(tag: String, message: String, exception: Throwable?) {
            QLog.logError(tag, message, exception)
        }
    }

    object Information : LogType {
        override val name: String = "INFO"
        override fun logPlatform(tag: String, message: String, exception: Throwable?) {
            PlatformLogger.logInfo(tag, message, exception)
        }

        override fun logQawaz(tag: String, message: String, exception: Throwable?) {
            QLog.logInfo(tag, message, exception)
        }
    }

    object Warning : LogType {
        override val name: String = "WARNING"
        override fun logPlatform(tag: String, message: String, exception: Throwable?) {
            PlatformLogger.logWarning(tag, message, exception)
        }

        override fun logQawaz(tag: String, message: String, exception: Throwable?) {
            QLog.logWarning(tag, message, exception)
        }
    }

    object Verbose : LogType {
        override val name: String = "VERBOSE"
        override fun logPlatform(tag: String, message: String, exception: Throwable?) {
            PlatformLogger.logVerbose(tag, message, exception)
        }

        override fun logQawaz(tag: String, message: String, exception: Throwable?) {
            QLog.logVerbose(tag, message, exception)
        }
    }

    object Weird : LogType {
        override val name: String = "WEIRD"
        override fun logPlatform(tag: String, message: String, exception: Throwable?) {
            PlatformLogger.logWeird(tag, message, exception)
        }

        override fun logQawaz(tag: String, message: String, exception: Throwable?) {
            QLog.logWeird(tag, message, exception)
        }
    }

    object WTF : LogType {
        override val name: String = "WTF"
        override fun logPlatform(tag: String, message: String, exception: Throwable?) {
            PlatformLogger.logWTF(tag, message, exception)
        }

        override fun logQawaz(tag: String, message: String, exception: Throwable?) {
            QLog.logWTF(tag, message, exception)
        }
    }
}