package com.wakaztahir.sketchable.components.overlays

import androidx.compose.runtime.Composable
import androidx.compose.ui.Modifier
import com.wakaztahir.sketchable.model.*

@Composable
fun ComposableOverlay(
    modifier: Modifier = Modifier,
    sketchableState: SketchableState,
    state: ComposableOverlayState
) {
    if (sketchableState.selectedLayer != null) {
        if (sketchableState.selectedLayer is Movable || sketchableState.selectedLayer is Resizable || sketchableState.selectedLayer is Rotatable) {
            TransformableOverlay(
                modifier = modifier,
                state = sketchableState,
                layer = sketchableState.selectedLayer!!,
                overlayState = state,
                onDismissRequest = {
                    sketchableState.deselectLayer()
                }
            )
        }
    }
}