package com.wakaztahir.sketchapp.screens

import androidx.compose.animation.ExperimentalAnimationApi
import androidx.compose.foundation.Canvas
import androidx.compose.foundation.background
import androidx.compose.foundation.gestures.detectTapGestures
import androidx.compose.foundation.isSystemInDarkTheme
import androidx.compose.foundation.layout.*
import androidx.compose.material3.ExperimentalMaterial3Api
import androidx.compose.material3.MaterialTheme
import androidx.compose.material3.Scaffold
import androidx.compose.material3.surfaceColorAtElevation
import androidx.compose.runtime.*
import androidx.compose.ui.Alignment
import androidx.compose.ui.ExperimentalComposeUiApi
import androidx.compose.ui.Modifier
import androidx.compose.ui.draw.clip
import androidx.compose.ui.input.pointer.pointerInput
import androidx.compose.ui.text.ExperimentalTextApi
import androidx.compose.ui.text.rememberTextMeasurer
import androidx.compose.ui.unit.dp
import androidx.compose.ui.zIndex
import com.wakaztahir.helpers.DialogBox
import com.wakaztahir.sketchable.canvas.drawSketch
import com.wakaztahir.sketchable.canvas.drawingPointerInput
import com.wakaztahir.sketchable.components.dialogs.ExportOptionsDialog
import com.wakaztahir.sketchable.components.dialogs.ExportOptionsState
import com.wakaztahir.sketchable.components.overlays.ComposableOverlay
import com.wakaztahir.sketchable.components.overlays.ComposableOverlayState
import com.wakaztahir.sketchable.components.tools.*
import com.wakaztahir.sketchable.importexport.export.ExportedSketch
import com.wakaztahir.sketchable.importexport.export.SketchableExporter
import com.wakaztahir.sketchable.importexport.import.SketchableImporter
import com.wakaztahir.sketchable.model.SketchableState
import com.wakaztahir.sketchable.serializer.SketchableSerializer
import com.wakaztahir.sketchable.utils.ZoomableState
import com.wakaztahir.sketchable.utils.zoomableDisplay
import com.wakaztahir.sketchapp.SketchApp
import com.wakaztahir.sketchapp.downloadFileWithContent
import com.wakaztahir.sketchapp.loadFileContentUsingSelectDialog
import com.wakaztahir.sketchapp.ui.theme.SketchableTheme
import kotlinx.coroutines.launch
import kotlinx.serialization.encodeToString

@OptIn(
    ExperimentalMaterial3Api::class, ExperimentalComposeUiApi::class, ExperimentalAnimationApi::class,
    ExperimentalTextApi::class
)
@Composable
fun EditorScreen(
    modifier: Modifier = Modifier,
    onBack: () -> Unit,
) {

    val zoomableState = remember { ZoomableState() }
    val sketchableState = remember { SketchableState(zoomableState) }
    val toolsState = remember { SketchableToolsState(sketchableState) }
    val overlayState = remember { ComposableOverlayState() }
    val textMeasurer = rememberTextMeasurer()

    var themeType by remember { mutableStateOf(SketchableThemeType.System) }
    var settingsDialog by remember { mutableStateOf(false) }
    var layersDialog by remember { mutableStateOf(false) }

    SketchableTheme(
        darkTheme = when (themeType) {
            SketchableThemeType.Light -> false
            SketchableThemeType.Dark -> true
            SketchableThemeType.System -> isSystemInDarkTheme()
        }
    ) {
        Scaffold(
            modifier = modifier,
            containerColor = MaterialTheme.colorScheme.background,
            topBar = {
                SketchableTools(
                    onBack = { onBack() },
                    sketchableState = sketchableState,
                    tools = toolsState,
                ) {
                    SettingsMenuItem(onClick = { settingsDialog = true })
                    LayersMenuItem(onClick = { layersDialog = true })
                    ExportSketchablesMenuItem(onClick = {
                        SketchApp.appScope.launch {
                            val exportedSketch = ExportedSketch(sketchableState.toSerializedState())
                            val json = SketchableSerializer.serializer.encodeToString(exportedSketch)
                            downloadFileWithContent(fileName = "MySketch.sketchable", type = "application/json", json)
                        }
                    })
                    ImportSketchableMenuItem(onClick = {
                        SketchApp.appScope.launch {
                            loadFileContentUsingSelectDialog(onLoaded = { content ->
                                SketchApp.appScope.launch {
                                    val importer = SketchableImporter(content as String)
                                    val exportedSketch = importer.import<ExportedSketch>()
                                    sketchableState.import(textMeasurer = textMeasurer, exportedSketch.state)
                                }
                            })
                        }
                    })
                }
            },
            bottomBar = {
                PaintBottomBar(
                    sketchableState = sketchableState,
                    tools = toolsState,
                    onAddImageClick = { }
                )
            }
        ) {
            Box(modifier = Modifier.padding(it)) {
                BoxWithConstraints(
                    modifier = Modifier
                        .fillMaxSize()
                        .zIndex(5f)
                        .drawingPointerInput(
                            sketchableState = sketchableState,
                            dispatchBrushEvents = true,
                            allowSingleTouchZoom = false
                        )
                ) {
                    Canvas(
                        modifier = Modifier
                            .fillMaxSize()
                            .zoomableDisplay(zoomableState)
                    ) {
                        drawSketch(sketchableState)
                    }
                }

                val toolsModifier = Modifier
                    .fillMaxWidth()
                    .align(Alignment.BottomCenter)
                    .zIndex(7f)
                    .padding(16.dp)
                    .background(
                        color = MaterialTheme.colorScheme.surfaceColorAtElevation(
                            4.dp
                        ),
                        shape = MaterialTheme.shapes.medium
                    )
                    .pointerInput(null) {
                        detectTapGestures {
                            toolsState.panelState =
                                ToolsPanelState.Hidden
                        }
                        awaitPointerEventScope { }
                    }

                ComposableOverlay(
                    modifier = Modifier
                        .fillMaxSize()
                        .zIndex(6f),
                    sketchableState = sketchableState,
                    state = overlayState
                )
                ToolsPanel(
                    modifier = toolsModifier,
                    tools = toolsState,
                    sketchableState = sketchableState
                )

            }
        }
        if (settingsDialog) {
            EditorSettingsDialog(
                onDismissRequest = { settingsDialog = false },
                state = sketchableState,
                themeType = themeType
            ) { themeType = it }
        }
        if (layersDialog) {
            DialogBox(onDismissRequest = { layersDialog = true }) {
                LayersPanel(
                    modifier = Modifier
                        .padding(16.dp)
                        .clip(MaterialTheme.shapes.medium)
                        .background(MaterialTheme.colorScheme.background)
                        .padding(horizontal = 8.dp, vertical = 8.dp),
                    state = sketchableState,
                    onDeleteLayer = {
                        // this is just to delete the layer's file
                    },
                    onDismissRequest = { layersDialog = false }
                )
            }
        }
    }
}