package com.wakaztahir.sketchable.brushes

import androidx.compose.runtime.Composable
import androidx.compose.ui.geometry.Offset
import androidx.compose.ui.graphics.*
import androidx.compose.ui.graphics.drawscope.DrawScope
import com.wakaztahir.sketchable.layers.ColoredPath
import com.wakaztahir.sketchable.model.Brush
import com.wakaztahir.sketchable.model.SketchableState
import com.wakaztahir.sketchable.model.UndoRedoAction
import com.wakaztahir.sketchable.utils.stringResource
import com.wakaztahir.sketchable.Res

open class RoundBrush(var size: Float = 8f) : Brush {

    override val properties: Brush.Properties
        get() = Properties

    protected var currentPath = ColoredPath(name = "Path 1", paintColor = Color.Black, paintStrokeWidth = size)
    private var pathAdded = false

    private var totalPaths = 1

    private var mCurX = 0f
    private var mCurY = 0f
    private var mStartX = 0f
    private var mStartY = 0f
    private var eventCanceled = false

    private fun getStrokeWidth(state: SketchableState): Float {
        return size
    }

    override fun handleDownEvent(state: SketchableState, position: Offset) {

        val requiredStrokeWidth = getStrokeWidth(state)

        val lastLayer = state.layers.lastOrNull() as? ColoredPath?
        if (lastLayer?.strokeWidth == requiredStrokeWidth && lastLayer.color == state.primaryColor) {
            currentPath = lastLayer
        } else if (currentPath.strokeWidth == requiredStrokeWidth && currentPath.color == state.primaryColor) {
            onSaveCurrentPath(state)
        } else {
            currentPath = ColoredPath(
                name = "Path $totalPaths",
                paintColor = state.primaryColor,
                paintStrokeWidth = requiredStrokeWidth
            )
            pathAdded = false
            onSaveCurrentPath(state)
        }

        mStartX = position.x
        mStartY = position.y
        currentPath.moveTo(position.x, position.y)
        mCurX = position.x
        mCurY = position.y
    }

    override fun handleMoveEvent(state: SketchableState, position: Offset) {
        if (eventCanceled) return
        currentPath.quadTo(mCurX, mCurY, (position.x + mCurX) / 2f, (position.y + mCurY) / 2f)
        mCurX = position.x
        mCurY = position.y
        currentPath.invalidate()
    }

    override fun cancelEvent(state: SketchableState) {
        eventCanceled = true
    }

    private fun onSaveCurrentPath(state: SketchableState) {
        if (!pathAdded) {
            state.layers.add(currentPath)
            pathAdded = true
            totalPaths++
        }
    }

    private fun addUpActionUndoState(state: SketchableState) {
        state.addUndoRedoAction(object : UndoRedoAction {
            val targetPath = currentPath
            var removed: IndexedValue<List<com.wakaztahir.sketchable.model.PathOperation>>? =
                null

            override fun action() {
                if (removed == null) {
                    removed = targetPath.removeLastPathOperations()
                } else {
                    targetPath.removePathOperations(removed!!.value)
                }
            }

            override fun opposite() {
                if (removed != null) {
                    targetPath.addPathOperations(removed!!.index, removed!!.value)
                }
            }
        })
    }

    override fun handleUpEvent(state: SketchableState, position: Offset) {
        // draw a dot on click
        if (!eventCanceled) {
            if (mStartX == mCurX && mStartY == mCurY) {
                currentPath.lineTo(mCurX, mCurY + 2)
                currentPath.lineTo(mCurX + 1, mCurY + 2)
                currentPath.lineTo(mCurX + 1, mCurY)
            } else {
                currentPath.lineTo(mCurX, mCurY) // todo why this line exists ?
            }
            addUpActionUndoState(state)
            currentPath.invalidate()
        } else {
            eventCanceled = false
        }
    }

    override fun DrawScope.draw(state: SketchableState) {
        with(drawContext.canvas) {
            with(currentPath) {
                draw(false)
            }
        }
    }

    companion object Properties : Brush.Properties {
        @Composable
        override fun brushName() : String = stringResource(Res.strings.round_brush)
        override fun DrawScope.drawPreview(color: Color) {
            val previewPath = ColoredPath(paintColor = color, paintStrokeWidth = 12f)
            previewPath.moveTo(
                x = size.width * 0.1f,
                y = size.height * 0.9f
            )
            previewPath.quadTo(
                x1 = size.width / 4f,
                y1 = - size.height / 4f,
                x2 = size.width * 0.5f,
                y2 = size.height * 0.5f
            )
            previewPath.quadTo(
                x1 = size.width * 0.75f,
                y1 = size.height + size.height / 4f,
                x2 = size.width * 0.9f,
                y2 = size.height * 0.1f
            )
            with(drawContext.canvas) {
                with(previewPath) {
                    draw(false)
                }
            }
        }
    }


}