package com.wakaztahir.sketchable.utils

import androidx.compose.ui.graphics.Color
import androidx.compose.ui.graphics.ImageBitmap
import androidx.compose.ui.graphics.Path
import androidx.compose.ui.unit.IntSize
import com.wakaztahir.sketchable.model.SketchableLayer
import com.wakaztahir.sketchable.model.SketchableState
import com.wakaztahir.sketchable.model.UndoRedoAction
import kotlin.math.PI
import kotlin.math.cos
import kotlin.math.sin

internal expect fun convertPathToImageBitmap(path: String): ImageBitmap

fun Color.toHex(): String {
    return "#${(this.value shr 32).toUInt().toString(16)}"
}

fun IntSize.getAspectRatioHeight(newWidth: Int = this.width) = height / width * newWidth
fun IntSize.getAspectRatioWidth(newHeight: Int = this.height) = width / height * newHeight


fun SketchableState.addLayerWithUndo(layer: SketchableLayer) {
    layers.add(layer)
    addUndoRedoAction(object : UndoRedoAction {
        override fun action() {
            layers.remove(layer)
        }

        override fun opposite() {
            layers.add(layer)
        }
    })
}

internal fun trianglePath(
    x: Float,
    y: Float,
    width: Float,
    height: Float
) = Path().apply {
    moveTo(x = x + width / 2f, y = y)
    lineTo(x = width + x, y = y + height)
    lineTo(x = x, y = y + height)
    close()
}

internal fun Path.drawPolygon(
    sides: Int,
    radius: Float,
    startX: Float,
    startY: Float,
) {
    val x0 = startX + (radius * cos(0.0).toFloat())
    val y0 = startY + (radius * sin(0.0).toFloat())

    //2.0 * Math.PI = 2π, which means one circle(360)
    //The polygon total angles of the sides must equal 360 degree.
    val angle = (2 * PI) / sides

    moveTo(x0, y0)

    for (s in 1 until sides) {

        lineTo(
            startX + (radius * cos(angle * s)).toFloat(),
            startY + (radius * sin(angle * s)).toFloat()
        )
    }

    close()
}