package com.wakaztahir.qawazlogger

actual object PlatformLogger {

    private fun intercept(type: LogType, tag: String, message: String, exception: Throwable?) {
        if(LogIntegrations.interceptors.isEmpty()) return
        for (interceptor in LogIntegrations.interceptors) {
            interceptor.intercept(type, tag, message, exception)
        }
    }

    actual fun logDebug(tag: String, message: String, exception: Throwable?) {
        console.log(tag, message, exception)
        intercept(LogType.Debug, tag, message, exception)
    }

    actual fun logError(tag: String, message: String, exception: Throwable?) {
        console.error(tag, message, exception)
        intercept(LogType.Error, tag, message, exception)
    }

    actual fun logInfo(tag: String, message: String, exception: Throwable?) {
        console.info(tag, message, exception)
        intercept(LogType.Information, tag, message, exception)
    }

    actual fun logWarning(tag: String, message: String, exception: Throwable?) {
        console.warn(tag, message, exception)
        intercept(LogType.Warning, tag, message, exception)
    }

    actual fun logVerbose(tag: String, message: String, exception: Throwable?) {
        console.log(tag, message, exception)
        intercept(LogType.Verbose, tag, message, exception)
    }

    actual fun logWeird(tag: String, message: String, exception: Throwable?) {
        console.error(tag, message, exception)
        intercept(LogType.Weird, tag, message, exception)
    }

    actual fun logWTF(tag: String, message: String, exception: Throwable?) {
        console.error(tag, message, exception)
        intercept(LogType.WTF, tag, message, exception)
    }

}