package com.wakaztahir.sketchable.layers

import androidx.compose.runtime.getValue
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.setValue
import androidx.compose.ui.geometry.Offset
import androidx.compose.ui.geometry.RoundRect
import androidx.compose.ui.graphics.*
import androidx.compose.ui.graphics.drawscope.DrawScope
import androidx.compose.ui.graphics.vector.ImageVector
import com.wakaztahir.sketchable.model.*
import com.wakaztahir.sketchable.model.PathOperation
import com.wakaztahir.sketchable.serializer.ColorSerializer
import com.wakaztahir.sketchable.serializer.SerializationHelper
import com.wakaztahir.sketchable.utils.toHex
import kotlinx.serialization.SerialName
import kotlin.math.max
import compose.icons.MaterialDesignIcons
import compose.icons.materialdesignicons.SineWave

open class ColoredPath(
    private val pathOperations: MutableList<PathOperation> = mutableListOf(),
    paintColor: Color,
    paintStrokeWidth: Float,
    override var name: String = "Colored Path"
) : SketchableLayer, SketchableExportableLayer {

    companion object {
        private fun offsetPathOperations(operations: List<PathOperation>, offset: Offset) {
            if (offset.x == 0f && offset.y == 0f) return
            for (op in operations) {
                when (op) {
                    is MoveToOp -> {
                        op.x += offset.x
                        op.y += offset.y
                    }
                    is LineToOp -> {
                        op.x += offset.x
                        op.y += offset.y
                    }
                    is QuadToOp -> {
                        op.x1 += offset.x
                        op.x2 += offset.x
                        op.y1 += offset.y
                        op.y2 += offset.y
                    }
                }
            }
        }

        private fun SketchableBounds.updateBounds(operations: List<PathOperation>, strokeWidth: Float) {
            val halfStrokeWidth = strokeWidth / 2f
            fun update(x: Float, y: Float) {
                left = minOf(x - halfStrokeWidth, left)
                top = minOf(y - halfStrokeWidth, top)
                right = maxOf(x + halfStrokeWidth, right)
                bottom = maxOf(y + halfStrokeWidth, bottom)
            }
            for (op in operations) {
                when (op) {
                    is LineToOp -> {
                        update(x = op.x, y = op.y)
                    }
                    is QuadToOp -> {
                        update(x = op.x2, y = op.y2)
                    }
                }
            }
        }
    }

    @SerialName("ColoredPath")
    @kotlinx.serialization.Serializable
    class Saved(
        val operations: MutableList<PathOperation>,
        val strokeWidth: Float,
        @kotlinx.serialization.Serializable(with = ColorSerializer::class)
        val color: Color,
        val name: String
    ) : SerializableLayer {
        override fun toLayer(offset: Offset, helper: SerializationHelper) = ColoredPath(
            pathOperations = operations.apply { offsetPathOperations(this, offset) },
            paintColor = this.color,
            paintStrokeWidth = this.strokeWidth,
            name = this.name
        )

        override fun updateBounds(bounds: SketchableBounds, helper: SerializationHelper) {
            bounds.updateBounds(operations, strokeWidth)
        }
    }

    override fun toSerializableLayer(offset: Offset) = Saved(
        operations = mutableListOf<PathOperation>().also { list ->
            for (op in operations) {
                list.add(op.clone())
            }
            offsetPathOperations(list, offset)
        },
        strokeWidth = this.strokeWidth,
        color = this.color,
        name = this.name
    )

    override fun applyOffset(offset: Offset) {
        if (offset.x == 0f && offset.y == 0f) return
        offsetPathOperations(pathOperations, offset)
        rebuildPath()
    }

    override fun icon(): ImageVector = MaterialDesignIcons.SineWave

    private val paint = Paint().apply {
        color = paintColor
        style = PaintingStyle.Stroke
        strokeJoin = StrokeJoin.Round
        strokeCap = StrokeCap.Round
        this.strokeWidth = paintStrokeWidth
    }

    val color: Color
        get() = paint.color

    val strokeWidth: Float
        get() = paint.strokeWidth

    override var isLocked: Boolean = false
    override var isVisible: Boolean = true

    private var invalidations by mutableStateOf(0L)

    private val path = Path().apply {
        if (pathOperations.isNotEmpty()) {
            applyPathOperations(pathOperations, this)
        }
    }

    val operations: List<PathOperation>
        get() = pathOperations

    fun clear() {
        pathOperations.clear()
        path.reset()
    }

    fun moveTo(x: Float, y: Float) {
        path.moveTo(x, y)
        pathOperations.add(MoveToOp(x = x, y = y))
    }

    fun lineTo(x: Float, y: Float) {
        path.lineTo(x, y)
        pathOperations.add(LineToOp(x = x, y = y))
    }

    fun quadTo(x1: Float, y1: Float, x2: Float, y2: Float) {
        path.quadraticBezierTo(x1 = x1, y1 = y1, x2 = x2, y2 = y2)
        pathOperations.add(QuadToOp(x1 = x1, y1 = y1, x2 = x2, y2 = y2))
    }

    override fun getColor(): Color? = paint.color

    override fun DrawScope.draw(inPreview: Boolean) {
        invalidations.let {
            drawContext.canvas.drawPath(
                path = path,
                paint = paint
            )
        }
    }

    private fun applyPathOperations(operations: List<PathOperation>, path: Path) {
        for (op in operations) {
            when (op) {
                is MoveToOp -> path.moveTo(x = op.x, y = op.y)
                is LineToOp -> path.lineTo(x = op.x, y = op.y)
                is QuadToOp -> path.quadraticBezierTo(
                    x1 = op.x1,
                    y1 = op.y1,
                    x2 = op.x2,
                    y2 = op.y2
                )
                else -> {
                    error("Unknown Path Operation")
                }
            }
        }
    }

    fun removeLastPathOperations(): IndexedValue<List<PathOperation>>? {
        if (pathOperations.isEmpty()) return null
        val removed = mutableListOf<PathOperation>()
        var startingIndex = max(pathOperations.size - 1, 0)
        for (i in (pathOperations.size - 1) downTo 0) {
            val operation = pathOperations[i]
            pathOperations.removeAt(i)
            removed.add(0, operation)
            if (operation is MoveToOp) {
                startingIndex = i
                break
            }
        }
        if (removed.isEmpty()) return null
        rebuildPath()
        return IndexedValue(startingIndex, removed)
    }

    fun removePathOperations(toRemove: List<PathOperation>) {
        pathOperations.removeAll(toRemove)
        rebuildPath()
    }

    fun addPathOperations(index: Int, operations: List<PathOperation>) {
        pathOperations.addAll(index, operations)
        rebuildPath()
    }

    fun removePathsIntersecting(
        point: Offset,
        radius: Float
    ): IndexedValue<List<PathOperation>>? {

        if (pathOperations.isEmpty()) return null

        val path = Path()
        var startingIndex = pathOperations.size - 1
        val tempOperations = mutableListOf<PathOperation>()
        val pointPath = Path()
        val intersectionCheckerPath = Path()

        pointPath.moveTo(point.x, point.y)
        pointPath.addRoundRect(
            roundRect = RoundRect(
                left = point.x - radius,
                right = point.x + radius,
                top = point.y - radius,
                bottom = point.y + radius,
                radiusX = radius,
                radiusY = radius
            )
        )

        fun isIntersecting(toCheck: Path): Boolean {
            intersectionCheckerPath.reset()
            intersectionCheckerPath.op(
                pointPath,
                toCheck,
                androidx.compose.ui.graphics.PathOperation.Intersect
            )
            return !intersectionCheckerPath.isEmpty
        }

        for (i in (pathOperations.size - 1) downTo 0) {
            when (val op = pathOperations[i]) {
                is MoveToOp -> {
                    tempOperations.add(0, op)
                    path.reset()
                    applyPathOperations(tempOperations, path)
                    if (isIntersecting(path)) {
                        startingIndex = i
                        removePathOperations(tempOperations)
                        break
                    } else {
                        tempOperations.clear()
                    }
                }
                is LineToOp -> {
                    tempOperations.add(0, op)
                }
                is QuadToOp -> {
                    tempOperations.add(0, op)
                }
                else -> {
                    error("Unknown Path Operation")
                }
            }
        }

        if (tempOperations.isEmpty()) return null

        return IndexedValue(startingIndex, tempOperations)
    }

    private fun rebuildPath() {
        path.reset()
        applyPathOperations(pathOperations, path)
        invalidations++
    }

    fun invalidate() {
        invalidations++
    }

    //todo fix this , path is only being exported as stroke
    override fun svgString(): String {
        var svgString =
            "<path " + "fill='none' " + "stroke-width='${paint.strokeWidth.toInt()}' " + "stroke='#${paint.color.toHex()}' " + "stroke-opacity='${paint.color.alpha}' " + "d='" // Beginning Path Tag

        //Drawing Path Operations
        pathOperations.forEach { op ->
            runCatching {
                when (op) {
                    is MoveToOp -> {
                        svgString += "M " + op.x.toInt() + " " + op.y.toInt() + " "
                    }
                    is LineToOp -> {
                        svgString += "L " + op.x.toInt() + " " + op.y.toInt() + " "
                    }
                    is QuadToOp -> {
                        svgString += "Q " + op.x1.toInt() + " " + op.y1.toInt() + " " + op.x2.toInt() + " " + op.y2.toInt() + " "
                    }
                }
            }.onFailure { it.printStackTrace() }
        }

        if (svgString[svgString.length - 1] == ' ') {
            svgString = svgString.substring(0, svgString.length - 1)
        }

        svgString += "' />" //Self Closing Path Tag

        return svgString
    }

}

