package com.wakaztahir.sketchable.importexport.import

import com.wakaztahir.sketchable.importexport.export.ExportedSketch
import com.wakaztahir.sketchable.serializer.SketchableSerializer
import kotlinx.serialization.decodeFromString
import org.w3c.files.File
import org.w3c.files.FileReader
import kotlin.coroutines.resume
import kotlin.coroutines.resumeWithException
import kotlin.coroutines.suspendCoroutine

actual class SketchableImporter private constructor(val source: Any) {

    constructor(json: String) : this(source = json)

    constructor(file : File) : this(source = file)

    suspend fun getFileContents(file : File) : dynamic {
        val reader = FileReader()
        reader.readAsText(file, "UTF-8")
        return suspendCoroutine<dynamic> { continuation ->
            reader.onload = { readerEvent ->
                continuation.resume((readerEvent.target as FileReader).result)
            }
            reader.onabort = {
                continuation.resumeWithException(Throwable("aborted reader"))
            }
            reader.onerror = {
                continuation.resumeWithException(Throwable("error reading file"))
            }
        }

    }


    actual suspend inline fun <reified T : ExportedSketch> import(): T {
        return when (source) {
            is File -> {
                val source = getFileContents(source)
                SketchableSerializer.serializer.decodeFromString(source as String)
            }

            is String -> {
                SketchableSerializer.serializer.decodeFromString(source)
            }

            else -> {
                throw IllegalArgumentException("Invalid source in importer")
            }
        }
    }
}