enum class ToolsPanelState {
    Hidden, BrushPanel, ColorsPanel
}

@OptIn(ExperimentalMaterial3Api::class)
@ExperimentalComposeUiApi
@ExperimentalAnimationApi
@Composable
fun SketchableTools(
    modifier: Modifier = Modifier,
    onBack : ()->Unit,
    sketchableState: SketchableState,
    tools: SketchableToolsState,
    menuItems: @Composable ColumnScope.() -> Unit = {},
) {

    var optionsMenu by remember { mutableStateOf(false) }

    fun toggleOptionsMenu() {
        optionsMenu = !optionsMenu
    }
    TopAppBar(
        modifier = modifier,
        colors = TopAppBarDefaults.smallTopAppBarColors(
            containerColor = MaterialTheme.colorScheme.surfaceColorAtElevation(3.dp).copy(.8f)
        ),
        navigationIcon = {
            if (sketchableState.selectedLayer == null) {
                IconButton(onClick = onBack) {
                    Icon(
                        imageVector = Icons.Default.ArrowBack,
                        contentDescription = null
                    )
                }
            }else {
                IconButton(onClick = { sketchableState.deselectLayer() }) {
                    Icon(
                        imageVector = Icons.Default.Close,
                        contentDescription = null
                    )
                }
            }
        },
        title = {

        },
        actions = {
            if (sketchableState.selectedLayer == null) {
                IconButton(onClick = { sketchableState.undo() }) {
                    Icon(
                        imageVector = MaterialDesignIcons.Undo,
                        contentDescription = "Undo Icon",
                        tint = LocalContentColor.current.copy(alpha = if (sketchableState.canUndo) 1.0f else 0.3f)
                    )
                }
                IconButton(onClick = { sketchableState.redo() }) {
                    Icon(
                        imageVector = MaterialDesignIcons.Redo,
                        contentDescription = "Redo Icon",
                        tint = LocalContentColor.current.copy(alpha = if (sketchableState.canRedo) 1.0f else 0.3f)

                    )
                }
                Column {
                    IconButton(onClick = ::toggleOptionsMenu) {
                        Icon(
                            imageVector = Icons.Default.MoreVert,
                            contentDescription = "Options Menu"
                        )
                    }
                    DropdownMenu(
                        expanded = optionsMenu, onDismissRequest = ::toggleOptionsMenu
                    ) {
                        menuItems()
                    }
                }
            } else {
                IconButton(onClick = {
                    sketchableState.deselectLayer()
                }) {
                    Icon(imageVector = Icons.Default.Check, contentDescription = null)
                }
            }
        })
}


@Composable
fun LayersMenuItem(onClick: () -> Unit) {
    DropdownMenuItem(
        onClick = onClick,
        leadingIcon = {
            Icon(
                imageVector = MaterialDesignIcons.LayersOutline,
