package com.wakaztahir.sketchable.components.tools

import androidx.compose.foundation.*
import androidx.compose.foundation.layout.*
import androidx.compose.foundation.shape.CircleShape
import androidx.compose.foundation.shape.RoundedCornerShape
import androidx.compose.material3.Icon
import androidx.compose.material3.MaterialTheme
import androidx.compose.runtime.*
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.draw.clip
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.unit.Dp
import androidx.compose.ui.unit.dp
import com.godaddy.android.colorpicker.ClassicColorPicker
import com.godaddy.android.colorpicker.HsvColor
import com.wakaztahir.sketchable.brushes.EraserBrush
import com.wakaztahir.sketchable.model.SketchableState
import com.wakaztahir.sketchable.utils.stringResource
import compose.icons.MaterialDesignIcons
import compose.icons.materialdesignicons.EyedropperVariant
import com.wakaztahir.sketchable.Res

@Composable
internal fun ColorsPanel(
    modifier: Modifier = Modifier,
    sketchableState: SketchableState,
    toolsState: SketchableToolsState,
    size: Dp = 30.dp,
) {

    var brushColorPickerDialog by remember { mutableStateOf(false) }

    Row(
        modifier = modifier.horizontalScroll(rememberScrollState()),
        verticalAlignment = Alignment.CenterVertically,
        horizontalArrangement = Arrangement.Center
    ) {
        Box(
            modifier = Modifier
                .clip(CircleShape)
                .clickable(onClick = { brushColorPickerDialog = true })
                .padding(8.dp)
        ) {
            Icon(
                modifier = Modifier.size(20.dp),
                imageVector = MaterialDesignIcons.EyedropperVariant,
                contentDescription = stringResource(Res.strings.color_picker_icon)
            )
        }
        for (color in toolsState.colors) {
            Box(
                modifier = Modifier
                    .padding(horizontal = 4.dp)
                    .width(size)
                    .height(size)
                    .clip(RoundedCornerShape(50))
                    .background(color)
                    .border(
                        2.dp,
                        if (color == sketchableState.primaryColor) {
                            MaterialTheme.colorScheme.onBackground
                        } else MaterialTheme.colorScheme.onBackground.copy(
                            0.1f
                        ),
                        RoundedCornerShape(50)
                    )
                    .clickable {
                        sketchableState.primaryColor = color
                        toolsState.panelState = ToolsPanelState.Hidden
                        if (sketchableState.brush is EraserBrush && toolsState.lastBrush != null) {
                            sketchableState.brush = toolsState.lastBrush!!
                        }
                    }
            )
        }
    }
    if (brushColorPickerDialog) {
        com.wakaztahir.helpers.DialogBox(onDismissRequest = { brushColorPickerDialog = false }) {
            ClassicColorPicker(
                modifier = Modifier.padding(36.dp).height(400.dp),
                color = HsvColor.from(Color.Red),
                onColorChanged = { c: HsvColor ->
                    val color = c.toColor()
                    sketchableState.primaryColor = color
                }
            )
        }
    }
}
