@Composable
fun BrushOptions(modifier: Modifier = Modifier, brush: Brush) {
    when (brush) {
        is RoundBrush -> RoundBrushOptions(modifier = modifier, brush = brush)
        is EraserBrush -> EraserBrushOptions(modifier = modifier, brush = brush)
    }
}

@Composable
fun BrushDrawPreview(
    modifier: Modifier = Modifier,
    brush: Brush.Properties,
    color: Color = Color.Black,
    background: Color = Color.White
) {
    Box(modifier = modifier
        .background(color = background)
        .drawWithContent {
            with(brush) {
                drawPreview(color = color)
            }
        }
    )
}