@Deprecated("compose implementation should be used when its available")
internal fun AwaitPointerEventScope.allPointersUp(): Boolean =
    !currentEvent.changes.any { it.pressed }

@Deprecated("compose implementation should be used when its available")
internal suspend fun AwaitPointerEventScope.awaitAllPointersUp() {
    if (!allPointersUp()) {
        do {
            val events = awaitPointerEvent(PointerEventPass.Final)
        } while (events.changes.any { it.pressed })
    }
}

@Deprecated("compose implementation should be used when its available")
internal suspend fun PointerInputScope.awaitEachGesture(block: suspend AwaitPointerEventScope.() -> Unit) {
    val currentContext = currentCoroutineContext()
    awaitPointerEventScope {
        while (currentContext.isActive) {
            try {
                block()

                // Wait for all pointers to be up. Gestures start when a finger goes down.
                awaitAllPointersUp()
            } catch (e: CancellationException) {
                if (currentContext.isActive) {
                    // The current gesture was canceled. Wait for all fingers to be "up" before
                    // looping again.
                    awaitAllPointersUp()
                } else {
                    // detectGesture was cancelled externally. Rethrow the cancellation exception to
                    // propagate it upwards.
                    throw e
                }
            }
        }
    }
}

fun Modifier.drawingPointerInput(
//    state: ZoomableState,
    sketchableState: SketchableState,
    dispatchBrushEvents: Boolean,
    allowSingleTouchZoom: Boolean,
    panZoomLock: Boolean = true,
): Modifier = this.pointerInput(
    sketchableState.zoomableState,
    sketchableState,
    dispatchBrushEvents,
    panZoomLock,
    allowSingleTouchZoom
) {

    val state = sketchableState.zoomableState
    val maxScale = state.maxScale
    val minScale = state.minScale
//    var sketchPointerId: Long? = null

    awaitEachGesture {

        var rotation = 0f
        var zoom = 1f
        var pan = Offset.Zero
        var pastTouchSlop = false
        val touchSlop = viewConfiguration.touchSlop
        var lockedToPanZoom = false

//        fun isBrushActive(): Boolean {
//            return dispatchBrushEvents && sketchableState.pageWidth != 0f && sketchableState.pageHeight != 0f
//        }

        val change = awaitFirstDown(requireUnconsumed = false)

        // Dispatching Down Events To Brush
//            if (!change.isConsumed) {
        if (dispatchBrushEvents) {
//            sketchPointerId = change.id.value
            sketchableState.brush.handleDownEvent(
                state = sketchableState,
                change.position.getTransformedPosition(
                    sketchableState.zoomableState,
                    sketchableState.viewportWidth,
                    sketchableState.viewportHeight
                )
            )
        }
//            }

        do {
            val event = awaitPointerEvent()
            val canceled = event.changes.any { it.isConsumed }
            if (event.changes.size > 1 && state.multiFingerZoom || sketchableState.mode == PointerMode.Zoom || allowSingleTouchZoom) {
                if (!canceled) {
//                    sketchPointerId = null
                    sketchableState.brush.cancelEvent(sketchableState)
                    val zoomChange = event.calculateZoom()
                    val rotationChange = event.calculateRotation()
                    val panChange = event.calculatePan()

                    if (!pastTouchSlop) {
                        zoom *= zoomChange
                        rotation += rotationChange
                        pan += panChange

                        val centroidSize = event.calculateCentroidSize(useCurrent = false)
                        val zoomMotion = abs(1 - zoom) * centroidSize
                        val rotationMotion = abs(rotation * PI.toFloat() * centroidSize / 180f)
                        val panMotion = pan.getDistance()

                        if (zoomMotion > touchSlop ||
                            rotationMotion > touchSlop ||
                            panMotion > touchSlop
                        ) {
                            pastTouchSlop = true
                            lockedToPanZoom = panZoomLock && rotationMotion < touchSlop
                        }
                    }

                    if (pastTouchSlop) {
//                            val centroid = event.calculateCentroid(useCurrent = false)
                        val effectiveRotation = if (lockedToPanZoom) 0f else rotationChange
                        if (effectiveRotation != 0f ||
                            zoomChange != 1f ||
                            panChange != Offset.Zero
                        ) {
                            state.onGesture(
                                state.rotateEnabled,
                                minScale,
                                maxScale,
                                panChange,
                                zoomChange,
                                effectiveRotation
                            )
                        }
                        event.changes.forEach {
                            if (it.positionChanged()) {
                                it.consume()
                            }
                        }
                    }
                }
            } else {
                // Dispatching Move Events To Brush
                if (dispatchBrushEvents) {
                    for (it in event.changes) {
//                        if (sketchPointerId == it.id.value) {
                        sketchableState.brush.handleMoveEvent(
                            state = sketchableState,
                            it.position.getTransformedPosition(
                                sketchableState.zoomableState,
                                sketchableState.viewportWidth,
                                sketchableState.viewportHeight
                            )
                        )
//                            sketchPointerId = it.id.value
//                        }
                    }
                }
            }
        } while (event.changes.any { it.pressed } && !canceled)

//            if (!change.isConsumed) {
        // Dispatching Up Events To Brush
        if (dispatchBrushEvents) {
            sketchableState.brush.handleUpEvent(
                state = sketchableState,
                change.position.getTransformedPosition(
                    sketchableState.zoomableState,
                    sketchableState.viewportWidth,
                    sketchableState.viewportHeight
                )
            )
        }
//        sketchPointerId = null
//            }
    }
}

fun Offset.getTransformedPosition(
    state: ZoomableState,
    containerWidth: Float,
    containerHeight: Float
): Offset {

//    Log.d("SketchNode", "x : ${position.x} , y : ${position.y} , scale : ${state.scale}")

    val centerX = containerWidth / 2f;
    val centerY = containerHeight / 2f;
    val relX = (this.x - state.offsetX) - centerX;
    val relY = (this.y - state.offsetY) - centerY;

    return Offset(
        x = ((relX / state.scale) + centerX),
        y = ((relY / state.scale) + centerY)
    )
}

fun Offset.getTransformedPositionInverse(
    state: ZoomableState,
    width: Float,
    height: Float
): Offset {

//    Log.d("SketchNode", "x : ${position.x} , y : ${position.y} , scale : ${state.scale}")

    val centerX = width / 2f
    val centerY = height / 2f

    val relXScale = this.x - centerX
    val relYScale = this.y - centerY

    val relX = relXScale * state.scale
    val relY = relYScale * state.scale

    return Offset(
        x = (relX + centerX) + state.offsetX,
        y = (relY + centerY) + state.offsetY
    )
}

fun ZoomableState.onGesture(
    rotate: Boolean,
    minScale: Float,
    maxScale: Float,
    pan: Offset,
    gestureZoom: Float,
    gestureRotate: Float
) {
    if (rotate) {
        angle += gestureRotate
    }
    val expectedScale = scale * gestureZoom
    if (expectedScale > minScale && expectedScale < maxScale) {
        scale = expectedScale
    }
    offsetX += pan.x
    offsetY += pan.y
}