package com.wakaztahir.sketchable.canvas

import androidx.compose.ui.geometry.Offset
import androidx.compose.ui.graphics.drawscope.DrawScope
import com.wakaztahir.sketchable.model.SketchableDrawing

internal fun DrawScope.trackViewportSize(state: SketchableDrawing) {

    if (state.viewportWidth == 0f && state.viewportHeight == 0f) {
        // initialized sketch , setting width & height
        state.updateViewportSize(this.size.width,this.size.height)
    } else if (state.viewportWidth != this.size.width && state.viewportHeight != this.size.height) {
        // page width has changed due to rotation of screen
        state.offsetBy(
            Offset(
                x = (this.size.width - state.viewportWidth) / 2f,
                y = (this.size.height - state.viewportHeight) / 2f
            )
        )
        state.updateViewportSize(this.size.width,this.size.height)
    }


}