package com.wakaztahir.sketchable.components.tools

import androidx.compose.foundation.layout.padding
import androidx.compose.runtime.Composable
import androidx.compose.ui.Modifier
import androidx.compose.ui.unit.dp
import com.wakaztahir.sketchable.brushes.EraserBrush
import com.wakaztahir.sketchable.model.SketchableState

@Composable
fun ToolsPanel(
    modifier: Modifier = Modifier,
    tools: SketchableToolsState,
    sketchableState: SketchableState
) {
    when (tools.panelState) {
        ToolsPanelState.ColorsPanel -> {
            ColorsPanel(
                modifier = modifier.padding(8.dp),
                sketchableState = sketchableState,
                toolsState = tools
            )
        }
        ToolsPanelState.BrushPanel -> {
            BrushPanel(
                modifier = modifier,
                sketchableState = sketchableState,
                displayBrushed = sketchableState.brush !is EraserBrush
            )
        }
        else -> {
            //todo doing nothing
        }
    }
}