private fun SketchableLayer.clone(helper: SerializationHelper): SketchableLayer? = try {
    val serialized = SketchableSerializer.serialize(listOf(this.toSerializableLayer(Offset.Zero)))
    SketchableSerializer.deserializeToLayers(serialized)[0].toLayer(Offset.Zero, helper)
} catch (e: Exception) {
    e.logIt()
    null
}

@OptIn(ExperimentalMaterial3Api::class, ExperimentalTextApi::class)
@Composable
fun LayersPanel(
    modifier: Modifier = Modifier,
    state: SketchableState,
    onDeleteLayer: (SketchableLayer) -> Unit,
    onDismissRequest: () -> Unit,
) {

    val textMeasurer = rememberTextMeasurer()
    val helper = remember {
        SerializationHelper(textMeasurer)
    }

    val reorderState = rememberReorderableLazyListState(onMove = { from, to ->
        val fromItem = state.layers[from.index]
        state.layers[from.index] = state.layers[to.index]
        state.layers[to.index] = fromItem
    })

    var deletingLayer by remember { mutableStateOf<SketchableLayer?>(null) }
    var renamingLayer by remember { mutableStateOf<SketchableLayer?>(null) }
    var optionsLayer by remember { mutableStateOf<SketchableLayer?>(null) }
    val updatedLayers = remember { mutableStateMapOf<SketchableLayer, SketchableLayer>() }

    Column(modifier = modifier) {
        Text(
            modifier = Modifier.padding(8.dp),
            text = stringResource(Res.strings.layers),
            style = MaterialTheme.typography.headlineSmall
        )
        LazyColumn(
            state = reorderState.listState,
            modifier = Modifier.reorderable(reorderState)
        ) {
            items(state.layers, { it.hashCode() }) { layer ->
                val updatedLayer = updatedLayers[layer] ?: layer
                LayerItem(
                    reorderState = reorderState,
                    layer = updatedLayer,
                    showSelect = layer is Movable && state.selectedLayer != layer,
                    onSelectLayer = {
                        state.selectLayer(layer)
                        optionsLayer = null
                        onDismissRequest()
                    },
                    showOptions = optionsLayer == layer,
                    onRenameLayer = {
                        renamingLayer = layer
                        optionsLayer = null
                    },
                    onToggleLock = {
                        layer.isLocked = it
                        layer.clone(helper)?.let { newLayer -> updatedLayers[layer] = newLayer }
                        optionsLayer = null
                    },
                    onToggleVisibility = {
                        layer.isVisible = it
                        layer.clone(helper)?.let { newLayer -> updatedLayers[layer] = newLayer }
                        optionsLayer = null
                    },
                    onToggleOptions = {
                        optionsLayer = if (optionsLayer == layer) {
                            null
                        } else {
                            layer
                        }
                    },
                    onDelete = {
                        deletingLayer = layer
                        optionsLayer = null
                    }
                )
                Spacer(modifier = Modifier.height(8.dp))
            }
        }
    }

    if (deletingLayer != null) {
        AlertDialog(
            onDismissRequest = { deletingLayer = null },
            title = { Text(text = stringResource(Res.strings.delete_layer)) },
            text = { Text(text = stringResource(Res.strings.delete_confirm_msg)) },
            confirmButton = {
                TextButton(onClick = {
                    state.layers.remove(deletingLayer!!)
                    onDeleteLayer(deletingLayer!!)
                    deletingLayer = null
                }) {
                    Text(text = stringResource(Res.strings.delete))
                }
            }
        )
    }

    if (renamingLayer != null) {
        var layerName by remember { mutableStateOf(renamingLayer!!.name) }
        AlertDialog(
            onDismissRequest = { renamingLayer = null },
            title = { Text(text = stringResource(Res.strings.rename_layer)) },
            text = {
                OutlinedTextField(
                    modifier = Modifier.fillMaxWidth(),
                    value = layerName,
                    onValueChange = {
                        layerName = it
                    },
                    singleLine = true
                )
            },
            confirmButton = {
                TextButton(onClick = {
                    renamingLayer!!.name = layerName
                    renamingLayer!!.clone(helper)
                        ?.let { newLayer -> updatedLayers[renamingLayer!!] = newLayer }
                    renamingLayer = null
                }) {
                    Text(text = stringResource(Res.strings.delete))
                }
            }
        )
    }


}

@Composable
internal fun LayerItem(
    modifier: Modifier = Modifier,
    reorderState: ReorderableState<*>,
    layer: SketchableLayer,
    showOptions: Boolean,
    showSelect: Boolean,
    onSelectLayer: () -> Unit,
    onRenameLayer: () -> Unit,
    onToggleLock: (Boolean) -> Unit,
    onToggleVisibility: (Boolean) -> Unit,
    onToggleOptions: () -> Unit,
    onDelete: () -> Unit,
) {
    Row(
        modifier = modifier
            .fillMaxWidth()
            .background(
                color = MaterialTheme.colorScheme.primary.copy(.3f),
                shape = MaterialTheme.shapes.medium
            )
            .padding(4.dp), verticalAlignment = Alignment.CenterVertically
    ) {
        Icon(
            modifier = Modifier.detectReorder(reorderState),
            imageVector = MaterialDesignIcons.Drag,
            contentDescription = stringResource(Res.strings.drag_icon),
            tint = MaterialTheme.colorScheme.onBackground
        )
        Text(
            modifier = Modifier.weight(1f),
            text = layer.name
        )
        Spacer(Modifier.width(8.dp))
        Icon(
            imageVector = layer.icon(),
            tint = layer.getColor() ?: LocalContentColor.current,
            contentDescription = null
        )
        Spacer(Modifier.width(8.dp))
        Column {
            IconButton(onClick = { onToggleOptions() }) {
                Icon(
                    imageVector = Icons.Default.MoreVert,
                    contentDescription = null,
                    tint = MaterialTheme.colorScheme.onBackground
                )
            }
            DropdownMenu(expanded = showOptions, onDismissRequest = onToggleOptions) {
                if (showSelect) {
                    DropdownMenuItem(
                        text = {
                            Text(text = "Select")
                        },
                        leadingIcon = {
                            Icon(
                                imageVector = Icons.Default.Check,
                                contentDescription = null,
                            )
                        },
                        onClick = onSelectLayer
                    )
                }
                DropdownMenuItem(
                    text = {
                        Text(text = "Rename")
                    },
                    leadingIcon = {
                        Icon(
                            imageVector = Icons.Default.Edit,
                            contentDescription = null,
                        )
                    },
                    onClick = onRenameLayer
                )
                DropdownMenuItem(
                    text = {
                        Text(text = if (layer.isVisible) "Make Invisible" else "Make Visible")
                    },
                    leadingIcon = {
                        Icon(
                            imageVector = if (layer.isVisible) MaterialDesignIcons.Eye else MaterialDesignIcons.EyeOutline,
                            contentDescription = null,
                            tint = MaterialTheme.colorScheme.onBackground
                        )
                    },
                    onClick = { onToggleVisibility(!layer.isVisible) }
                )
                DropdownMenuItem(
                    text = {
                        Text(text = if (layer.isLocked) "Unlock Layer" else "Lock Layer")
                    },
                    leadingIcon = {
                        Icon(
                            imageVector = if (layer.isLocked) MaterialDesignIcons.LockOpenVariant else MaterialDesignIcons.Lock,
                            contentDescription = null,
                        )
                    },
                    onClick = { onToggleLock(!layer.isLocked) }
                )
                DropdownMenuItem(
                    text = {
                        Text(text = "Delete")
                    },
                    leadingIcon = {
                        Icon(
                            imageVector = MaterialDesignIcons.TrashCan,
                            contentDescription = null,
                            tint = MaterialTheme.colorScheme.onBackground
                        )
                    },
                    onClick = onDelete
                )
            }
        }
    }
}