package com.wakaztahir.sketchable.utils

import androidx.compose.foundation.gestures.rememberTransformableState
import androidx.compose.foundation.gestures.transformable
import androidx.compose.foundation.layout.wrapContentSize
import androidx.compose.runtime.getValue
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.setValue
import androidx.compose.ui.Modifier
import androidx.compose.ui.composed
import androidx.compose.ui.graphics.graphicsLayer
import androidx.compose.ui.layout.layout
import androidx.compose.ui.unit.Constraints

class ZoomableState {
    var multiFingerZoom by mutableStateOf(true)
    var rotateEnabled by mutableStateOf(false)
    var angle by mutableStateOf(0f)
    var scale by mutableStateOf(1f)
    var offsetX by mutableStateOf(0f)
    var offsetY by mutableStateOf(0f)
    var maxScale = 16f
    var minScale = 0.01f

    fun reset(){
        angle = 0f
        scale = 1f
        offsetX = 0f
        offsetY = 0f
    }

}


/**
 * This scales the sketchable down to its preview composable and renders it properly
 */
fun Modifier.previewMap(): Modifier {
    return this
        .layout { measurable, constraints ->
            val r =
                measurable.measure(constraints = constraints.copy(maxWidth = Constraints.Infinity))
            layout(r.measuredWidth, r.measuredHeight, placementBlock = {
                r.placeWithLayer(0, 0, 0f) {
                    calculateFitScale(
                        mapWidth = r.measuredWidth,
                        mapHeight = r.measuredHeight,
                        viewportWidth = constraints.maxWidth.toFloat(),
                        viewportHeight = constraints.maxHeight.toFloat(),
                    ).let {
                        scaleX = it
                        scaleY = it
                    }
                }
            })
        }
}

/**
 * This makes map Zoomable , Pannable
 */
fun Modifier.zoomableMap(
    zoomableState: ZoomableState,
    enabled: Boolean = true,
    rotate: Boolean = false,
    panZoomLock: Boolean = true
): Modifier = this
    // Making the sketchable render fully without respecting any constraints
    .layout { measurable, constraints ->
        val r = measurable.measure(constraints = constraints.copy(maxWidth = Constraints.Infinity))
        layout(r.measuredWidth, r.measuredHeight, placementBlock = {
            r.placeWithLayer(0, 0, 0f)
        })
    }
    .wrapContentSize(unbounded = true)
    .zoomable(zoomableState, enabled, rotate, panZoomLock)


/**
 * Zoomable Display Modifier
 */
fun Modifier.zoomableDisplay(zoomableState: ZoomableState) = this.graphicsLayer(
    scaleX = zoomableState.scale,
    scaleY = zoomableState.scale,
    rotationZ = zoomableState.angle,
    translationX = zoomableState.offsetX,
    translationY = zoomableState.offsetY
)

internal fun Modifier.zoomableInput(
    zoomableState: ZoomableState,
    enabled: Boolean = true,
    rotate: Boolean = false,
    zoomRotationLock: Boolean = true,
) = composed {
    this.transformable(state = rememberTransformableState { zoomChange, panChange, rotationChange ->
        zoomableState.offsetX += panChange.x
        zoomableState.offsetY += panChange.y
        zoomableState.scale *= zoomChange
        if (rotate) {
            zoomableState.angle += rotationChange
        }
    }, lockRotationOnZoomPan = zoomRotationLock, enabled = enabled)
}

/**
 * Zoomable Modifier to make component zoomable
 */
internal fun Modifier.zoomable(
    zoomableState: ZoomableState,
    enabled: Boolean = true,
    rotate: Boolean = false,
    panZoomLock: Boolean = true
): Modifier = this
    .zoomableDisplay(zoomableState)
    .zoomableInput(zoomableState, enabled, rotate, panZoomLock)




/**
 * should call [MapperState.fitInViewport] instead
 * Fits the Map inside viewport width & viewport height
 */
fun ZoomableState.fitInViewport(
    mapWidth: Int,
    mapHeight: Int,
    viewportWidth: Float,
    viewportHeight: Float,
    changeScale: Boolean = true,
    changeMinScale: Boolean = true,
    changeOffset: Boolean = true,
) {

    // Dividing Scale For Spacing
    val scale = calculateFitScale(mapWidth, mapHeight, viewportWidth, viewportHeight)
    if (changeScale) {
        this.scale = scale / 1.1f
    }
    if (changeMinScale) {
        minScale = scale / 1.5f
    }
    // Setting Center Of Map , Not Central Node
    if (changeOffset) {
        offsetX = 0f
        offsetY = 0f
    }
}


/**
 * Calculates scale for map according to screen width & height to fit it
 */
private fun calculateFitScale(
    mapWidth: Int,
    mapHeight: Int,
    viewportWidth: Float,
    viewportHeight: Float
): Float {
    val usingHeight =
        mapHeight > mapWidth
    val nominator = if (usingHeight) {
        viewportHeight
    } else {
        viewportWidth
    }
    val denominator = if (usingHeight) {
        mapHeight
    } else {
        mapWidth
    }
    return nominator / denominator.toFloat()
}

